html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
}

.card {
  width: 400px;
  margin: 20px auto;
  padding: 20px;
  border: 2px solid #0d0d0d;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  position: relative;
}

.card-title {
  font-family: 'Amatic SC', cursive;
  font-size: 24px;
  margin-bottom: 20px;
  margin-top: 10px;
  margin-left: 10%;
}

.progress-bar {
  height: 10px;
  background-color: #dfdfdf;
  border-radius: 5px;
  width: 82%;
  margin: 0 auto;
  margin-bottom: 15px;
}

.progress-indicator {
  height: 100%;
  background-color: #0d0d0d;
  transition: width 0.3s ease-in-out;
  border-radius: 5px;
}

.card-content {
  overflow-y: scroll;
  scrollbar-width: thin;
  scrollbar-color: darkgrey lightgrey;
  text-align: left;
  max-height: 500px;
}

.card-content::-webkit-scrollbar {
  width: 10px;
  margin-right: 10px;
}

.card-content::-webkit-scrollbar-track {
  background-color: #dfdfdf;
  border-radius: 5px;
  margin-top: 20px;
}

.card-content::-webkit-scrollbar-thumb {
  background-color: #0d0d0d;
  border-radius: 5px;
}

form {
  width: 80%;
  margin: 0 auto;
}


input,
select,
textarea {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  box-sizing: border-box;
}

input[type="checkbox"] {
  position: relative;
  appearance: none;
  cursor: pointer;
}

input[type="checkbox"]::before {
  content: '';
  display: inline-block;
  width: 16px;
  height: 16px;
  border: 2px solid #0d0d0d;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 3px;
  transition: background-color 0.3s ease-in-out;
}

input[type="checkbox"]:checked::before {
  background-color: #0d0d0d;
  border-color: #0d0d0d;
}

.container{
  display: flex;
}

button {
  padding: 10px;
  background-color: #0d0d0d;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: #0d0d0d;
}


.default{
  display: block;
  margin-bottom: 10px;
  margin-top: 20px;
  position: relative;
  padding: 4px;
  background-color: rgb(235, 235, 235)
}
.default::before {
  content: '';
  display: block;
  width: 9px;
  height: 100%;
  background-color: #C0DA5D;
  position: absolute;
  top: 0;
  left: -10px;
  border-radius: 3px 0 0 3px;
}